import { useCallback, useEffect, useRef, useState } from "react";
import classnames from "classnames";
import spreadImg from "../../../asset/images/request_bg.png";
import "./section7.scss";
import Fade from "react-reveal/Fade";

export default function Section7() {
  const [spread, setSpread] = useState();
  const videoBoxRef = useRef();
  const handleScroll = useCallback(() => {
    const BoundingClientRect = videoBoxRef.current.getBoundingClientRect().y;
    console.log(BoundingClientRect);
    if (BoundingClientRect <= 800) {
      console.log(1);
      setSpread((prev) => true);
    } else {
      setSpread((prev) => false);
    }
  }, []);

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
  }, []);
  return (
    <div className="section7-ziple-pf-request">
      <h1 className="section7-ziple-tech-headline">
        {" "}
        <h2> </h2>   
        <div ref={videoBoxRef} className="section7-ziple-spread-box">
          <span
            className={classnames("section7-ziple-spread-left", {
              cancle: spread,
            })}
          ></span>
          <span
            className={classnames("section7-ziple-spread-right", {
              cancle: spread,
            })}
          ></span>
          <Fade bottom duration={2000}>
            <h1 className="section7-ziple-spread-txt">
                  
            </h1>
          </Fade>
          <span className="section7-ziple-spread-btn"></span>
          <img src={spreadImg} />
        </div>
      </h1>
    </div>
  );
}
